import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import Navegacion from '../Navegacion/Navegacion';
import auth from '../../helpers/auth';
import roles from '../../helpers/roles';
import './Inicio.css';
import sanjose from '../../assets/sanjose.jpg';

export default class Inicio extends Component {
  constructor(props) {
    super(props);
    this.state = {
      redirect: false
    };
  }

  componentDidMount() {
    auth.verifyToken()
      .then(res => {
        if (res) {
          roles.checkRoles()
            .then(() => {
              this.setState({
                redirect: true
              });
            })
            .catch((err) => console.log(err));
        }
      })
      .catch((err) => console.log(err));
  }

  render() {
    return (this.state.redirect ? <Redirect to='/consejos' /> :
      <>
        <Navegacion />
        <div className="row m-0 my-row">
          <div className="col-sm-12 my-auto">
            <div className="container bounce">
              <h3 className="title text-center">Consejo de Ingeniera en Computacin<br /> del Campus Tecnolgico Local de San Jos</h3>
              <div className="special-div">
                <img src={sanjose} className="rounded img-fluid handler" alt="Escuela de Computacin San Jos" />
                <p className="description">La Escuela de Ingeniera en Computacin ha creado un slido prestigio como centro de excelencia en la enseanza y en la investigacin aplicada. Ofrece opciones acadmicas en la Sede Central en Cartago ,  y en las Sedes de San Carlos, Alajuela, Limn y San Jos.<br />
                        Su misin es: Contribuir a la sociedad costarricense en la generacin de conocimiento cientfico-tecnolgico en computacin a travs de la docencia, investigacin, extensin y vinculacin externa, basados en principios de excelencia acadmica, pertinencia social, regionalizacin, equidad y formacin integral.</p>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}
